$sizeh2big:1.5rem;
$sizeh2small:0.5rem;
.header{z-index: 13;
    background: rgb(0, 0, 0);
    box-sizing: border-box;
    position:sticky;
    box-shadow: 1px 1px 70px 90px rgba(0,0,0,0.34) inset,inset 0px -38px 28px -7px rgba(0,0,0,0.5);
    top:0;
    left:0;
    padding: 0 0;
    // padding-left:  1rem;
    // padding-right: 1rem;
    // padding-top: 0.67rem;
    // max-width: 375;
    height:300px;
.header_content{
    // padding-left:  1rem;
    // padding-right: 1rem;
    // padding-top: 0.67rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 300px;
    max-height: 300px;
    .content{ 
       z-index: 2000;
    // position: fixed;
    // top: 0;
    // left: 0;
    padding:0px 3%  ;
       width:calc(50%);
        // width: max-content;
        color: whitesmoke;
        height: 287px;
        max-height: 300px; 
        overflow-y: scroll; 
        // background-color:rgba(0, 0, 0, 0.2);
        &.scrollable{
            overflow-y: scroll; 
             height: 300px;
            max-height: 300px;
        }
        
    
     h2{
        background-image: linear-gradient(to top right, rgb(175, 174, 174),#4c0082ca);
         box-shadow: 7px -5px 10px 0px #4B0082, 11px -9px 10px 0px #0000FF, 16px -14px 10px 0px #00FF00, 20px -17px 10px 0px #FFFF00, 24px -19px 10px 0px #FF7F00, 27px -23px 10px 0px #FF0000, 5px 5px 15px 5px rgba(0,0,0,0);
            max-width: max-content;
        }
     h3{
         padding-left: 30px;
         

     }
     
    }
    .contentempty{ 
        display:flex;
        flex-wrap: wrap;
        z-index: 2000;
    //  position: fixed;
    //  top: 0;
    //  left: 0;
     padding:0px 40px  ;
        width:calc(100% - 80px);
         // width: max-content;
         color: whitesmoke;
         background-color:rgba(0, 0, 0, 0.2);
         overflow-x: hidden;
         overflow-y: scroll;
      height: 300px;
      max-height: 300px;
      .name{
            position: sticky;
             top: 0;
          width: max-content;
          min-width: 375px;
          height: 300px;
          max-height: 300px;
          a{
            color:white;
        }
        .curri{
            position:relative;
            top: -180px;
            left:180px;
            font-size: 2rem;
            a{
                color:rgb(255, 230, 6);
            }
        }
          .title {
          display: flex;
          flex-wrap: wrap;
          
          h2{
              padding-right: $sizeh2big;
              color:rgb(133,241,65);
              width: max-content;
              
          }
      }
      } 
     }
     .resume{
         max-width: 600px;
         video{
                 height: 275px;
             }
     }
    .backcarousel{
    //     position: fixed;
    // top: 0;
    // right: 0;
        width:44%;
        display: flex;
        justify-content:center;
        align-items: center;
        // height: 100%;
        height: 300px;
        background-color:rgba(0, 0, 0, 0.2);
        overflow: hidden;
    }
    .videoexplain,.box_video{
        position: fixed;
        top: 0;
        right: 0;
            width:44%;
            display: flex;
            justify-content:center;
            align-items: center;
            // height: 100%;
            height: 300px;
            // background-color:rgba(0, 0, 0, 0.2);
            video{
                height: 300px;
                max-width: 100%;
            }
    }
}
   
    .return-arrow {
      
        z-index: 3000;
        position: absolute;
        top:0px;
        right: 20px;
         text-align: right;
        img{
          
            height: 20%;
            width: 20%;
       }
       &:hover{
           cursor: grab;
       }
       
    }
       
}
