.App{
    .screensplash{
        display: flex;
        position:fixed;
        padding:0;
        margin:0;
    
        top:0;
        left:0;
    
        width: 100%;
        height: 100%;
        background:rgb(0, 0, 0);
       
        video{
            width: 100%;
        }
        .star1{
             z-index: 1000;
            position:fixed;
            overflow: hidden;
        padding:0;
        margin:0;
    
        top:0;
        left:0;
    
        width: 100%;
        height: 2000px;
        background-image:url(../media/sky2.png);
        animation: fall1 120s linear infinite;
    }
        .star2{
            z-index: 1002;
           position:fixed;
           overflow: hidden;
       padding:0;
       margin:0;
   
       top:0;
       left:0;
   
       width: 2000px;
       height: 2000px;
       background-image:url(../media/sky3.png);
       animation: fall2 50s linear infinite;}
       .star3{
        z-index: 1003;
       position:fixed;
   padding:0;
   margin:0;

   top:-250px;
   left:-250px;

   width:2000px;
   height: 2000px;
   overflow: hidden;
   opacity: 0.7;
   background-image:url(../media/sky4.png);
   animation: fall3 50s linear infinite;}    
    }
    @keyframes fall1 {
        0%{
            transform: translateY(-1000px);
        }
    }
    @keyframes fall2 {
        0%{
            transform: translateY(-600px) translateX(-100px);
        }
        
    }
    @keyframes fall3 {
        0%{
            transform: translateY(300px) translateX(150px);
        }
        
    }
}